/* üîπ Desktop Sidebar */
.sidebar {
  width: 250px;
  background-color: #002366;
  color: white;
  padding: 20px;
  height: 100vh;
}

.sidebar h2 {
  text-align: center;
  margin-bottom: 30px;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  margin-bottom: 15px;
}

.sidebar a {
  color: white;
  text-decoration: none;
  padding: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
  border-radius: 8px;
}

.sidebar a:hover {
  background-color: #1e3a8a;
}

/* üîπ Mobile Navbar */
.mobile-navbar {
  display: none;
  background: #002366;
  color: white;
  padding: 12px 20px;
  align-items: center;
  justify-content: space-between;
}

.menu-toggle {
  font-size: 22px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.mobile-title {
  font-size: 18px;
  font-weight: 600;
}

/* üîπ Mobile Dropdown Menu */
.mobile-menu {
  display: none;
  background: #002366;
  color: white;
  position: absolute;
  top: 56px;
  left: 0;
  width: 100%;
  z-index: 99;
  animation: slideDown 0.3s ease-in-out;
}

.mobile-menu ul {
  list-style: none;
  margin: 0;
  padding: 10px 0;
}

.mobile-menu li {
  border-bottom: 1px solid rgba(255,255,255,0.2);
}

.mobile-menu a {
  color: white;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
}

.mobile-menu a:hover {
  background: #1e3a8a;
}

/* Animation */
@keyframes slideDown {
  from { opacity: 0; transform: translateY(-20px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* üîπ Responsive Breakpoints */
@media (max-width: 768px) {
  .sidebar {
    display: none;   /* Hide sidebar in mobile */
  }

  .mobile-navbar {
    display: flex;   /* Show top navbar */
  }

  .mobile-menu {
    display: block;  /* Show dropdown when open */
  }
}
